import React, { useState, useEffect, useRef } from "react";
import { Form } from "react-bootstrap";
import styles2 from "../css/COLUMN.module.css";
import ElemDeleteControl from "./ElemDeleteControlWrapper";
import Checkbox from "@material-ui/core/Checkbox";
import Tooltip from "../../ReusableComponents/Tooltip/Tooltip";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import styles3 from "../css/CHECKBOX.module.css";
import { callZoneGotFocusWithPreHook } from "../../Resources/globalFunctions";
import PrepareCSSClass from "../../components/IFormDesignerLayout/PrepareCSSClass";
import EditableLabel from "../../ReusableComponents/EditableLabel/EditableLabel";
import { sendRequestToUpdateValue } from "../../components/IFormDesignerLayout/RequestLogic";
import QuickTools from "../../ReusableComponents/QuickTools/QuickTools";
import { triggerEventCalls } from "../../ReusableComponents/Events/EventExecutionLogic";
import { getSuggestedId } from "../../components/IFormDesignerLayout/Logic/UserIdLogic";
import { checkValid } from "../../Resources/globalFunctions";
import { useDispatch } from "../../Redux";
import CustomizedAlert from "../../ReusableComponents/Alert/CustomizedAlert";
import {
  handleDisplayProperty,
  setMandatoryControls,
  updateValue,
} from "../../components/IFormDesignerLayout/IFormDesignerLogic";
import {
  GetState,
  GetStateFromData,
} from "../../components/CustomHooks/CustomHooks";
import MandatoryMsg from "../../ReusableComponents/ErrorMessageComponent/MandatoryMsg";
import { cloneDeep } from "lodash";
import { updateGridContexts } from "../../Redux/Actions/FormStateActions";

const CHECKBOX = (props) => {
  const contexts = GetStateFromData(props.containerId);
  const gridContexts = GetStateFromData(props.containerId, "data2");

  const { tempCanWidth, controlSelected, mandatoryControls } = GetState(
    "tempCanWidth",
    "controlSelected",
    "mandatoryControls"
  );

  const { openCanvasForPreview, listviewClearState, selectedRowIndex } =
    gridContexts.gridStates;

  const reduxContexts = contexts;
  const {
    IdPrecedence,
    modeReadOrWrite,
    deleteControl,
    openQuickToolFor,
    formState2,
    listviewColumnInfo,
    device,
    listviewInfoUpdated,
    themeOpen,
    themeResetDone,
    themeResetValDone,
  } = contexts.contexts;
  const conditionalQuickToolOpening =
    props.id === openQuickToolFor ? true : false;

  const dispatch = useDispatch();
  const themeData = formState2.theme;
  const controlThemeData = themeData?.["InputStyle"];
  const translationStyles1 =
    window?.depsatkur_configs?.selectedLanguage === "ar" ||
    window?.depsatkur_configs?.selectedLanguage === "ar-SA";
  const propsDataStyle = props.data.style;

  const defaultStyle = {
    styleLayout: "Style1",
    stateColor: "#707070",
    fontFamily: "Open Sans",
    color: "#000000",
    fontSize: "14px",
    fontWeight: "400",
    fontStyle: "bold",
    display: "flex",
    position: "relative",
    paddingBottom: "0px",
    paddingTop: "0px",
    paddingLeft: "0px",
    paddingRight: "0px",
    inActiveStateColor: "rgba(206,217,224,.5)",
  };
  const checkBoxThemeStyle = useRef(defaultStyle);
  const defaultBuffer2 = cloneDeep(defaultStyle);

  const [showMandatoryMsg, setShowMandatoryMsg] = useState(false);
  useEffect(() => {
    if (mandatoryControls.includes(props.data?.id)) {
      setShowMandatoryMsg(true);
    } else {
      setShowMandatoryMsg(false);
    }
  }, [mandatoryControls]);

  const actualControlId = props.data.id;
  let mappingId = props.data["style"] && props.data["style"]["MappingId"];
  let userDefinedId = props.data["style"] && props.data["style"]["CustomId"];
  let userDefinedControlId = getSuggestedId(
    actualControlId,
    mappingId,
    userDefinedId,
    IdPrecedence
  );
  const [open, setOpen] = useState(conditionalQuickToolOpening);

  const data = props.data;
  const [checkedState, setCheckedState] = useState(
    props.data.value === "true" || props.data.value === true ? true : false
  );
  const elementRefrence = useRef(null);
  const [defaultLabel, setDefaultLabel] = useState(
    props?.data?.style?.["Label"] || props?.data?.style?.["Label"] === ""
      ? props?.data?.style?.["Label"]
      : props?.data?.id
  );
  const [editLabel, setEditLabel] = useState(false);
  const [cloneThemeStyle, setCloneThemeStyle] = useState();

  const useStyles = makeStyles({
    rootMain: {
      width: "100%",
      position: "relative",

      "& .MuiSvgIcon-root": {
        width:
          parseInt(checkBoxThemeStyle?.current?.fontSize?.replace("px", "")) +
          8 +
          "px",
        fontSize:
          parseInt(checkBoxThemeStyle?.current?.fontSize?.replace("px", "")) +
          8 +
          "px",
        height:
          parseInt(checkBoxThemeStyle?.current?.fontSize?.replace("px", "")) +
          8 +
          "px",
      },
    },
  });

  useEffect(() => {
    if (
      props.data.style &&
      (props?.data?.style?.["Label"] || props?.data?.style?.["Label"] === "")
    ) {
      setDefaultLabel(props.data.style["Label"]);
    }
  }, [props.data.style["Label"]]);

  useEffect(() => {
    if (themeOpen === true || themeOpen === "true") {
      setCloneThemeStyle(cloneDeep(checkBoxThemeStyle.current));
    }
  }, [themeOpen]);

  useEffect(() => {
    if (themeResetDone === true || themeResetDone === "true") {
      Object.assign(checkBoxThemeStyle.current, cloneThemeStyle);
    }
  }, [themeResetValDone]);

  const [mobileDisplay, setMobileDisplay] = useState(true);
  const [tabDisplay, setTabDisplay] = useState(true);

  const classes = useStyles(props.data.style);

  if (props.previewmode && modeReadOrWrite === "r") {
    props.data.style.Enable = "false";
  }

  const isSubformInPage = props?.isSubformInPage || false;

  const startMobile = useRef();
  const endMobile = useRef();
  const startTab = useRef();
  const endTab = useRef();

  useEffect(() => {
    if (formState2 && formState2.style) {
      startMobile.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]
      );
      endMobile.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]
      );
      startTab.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]
      );
      endTab.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]
      );
    }
  }, [
    formState2.style?.["Breakpoint"],
    formState2.style?.["Breakpoint"]?.["Mobile"],
    formState2.style?.["Breakpoint"]?.["Tab"],
  ]);

  useEffect(() => {
    if (props?.previewmode)
      handleDisplayProperty(
        props,
        device,
        setMobileDisplay,
        setTabDisplay,
        startMobile.current,
        endMobile.current,
        startTab.current,
        endTab.current
      );
  }, [
    props?.data?.style?.["MobileVisible"],
    props?.data?.style?.["TabsVisible"],
    device,
    endMobile,
    endTab,
    startMobile,
    startTab,
    tempCanWidth,
  ]);

  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification",
  });

  function showAlertConfirmNotifications(dataTobeSet) {
    setAlertData(dataTobeSet);
  }
  const bringEditableLabel = (event) => {
    setEditLabel(true);
  };
  const setLabelHandler = (recievedLabel) => {
    try {
      if (
        props.data &&
        props?.data?.style &&
        (props?.data?.style?.Label || props?.data?.id)
      ) {
        setDefaultLabel((prevDefaultLabel) => {
          if (prevDefaultLabel === recievedLabel) {
            return prevDefaultLabel;
          } else {
            props.data.style["Label"] = recievedLabel;
            return recievedLabel;
          }
        });
        setEditLabel(false);
      }
    } catch (err) {
      console.error(err);
    }
  };

  useEffect(() => {
    if (
      props.previewmode &&
      props.data &&
      props.data.value &&
      props.data.value !== null &&
      props.data.value !== undefined &&
      props.data.value !== ""
    ) {
      if (props?.data?.value?.toString()?.toLowerCase() === "true")
        setCheckedState(true);
      else setCheckedState(false);
    } else {
      setCheckedState(false);
    }

    if (
      openCanvasForPreview === "showDataAdditonSection" ||
      window.isAboveSection || props.data.isAboveListview
    ) {
      if (
        listviewColumnInfo.get(props?.data?.style?.ColumnName) == "true" ||
        listviewColumnInfo.get(props?.data?.style?.ColumnName) == true
      ) {
        listviewColumnInfo.set(props?.data?.style?.ColumnName, true);
      } else {
        listviewColumnInfo.set(props?.data?.style?.ColumnName, false);
      }
    }
  }, [props.data.value]);

  useEffect(() => {
    const tableId = props.data?.id?.split("_")?.[0];
    const currentListviewColumnInfo = listviewColumnInfo.get(tableId);
    if (currentListviewColumnInfo) {
      if (currentListviewColumnInfo.size > 0) {
        if (
          currentListviewColumnInfo.get(props.data.style.ColumnName) !==
          undefined
        ) {
          if (
            currentListviewColumnInfo.get(props.data.style.ColumnName) ===
              "false" ||
            currentListviewColumnInfo.get(props.data.style.ColumnName) === false
          ) {
            setCheckedState(false);
          } else if (
            currentListviewColumnInfo.get(props.data.style.ColumnName) ===
              "true" ||
            currentListviewColumnInfo.get(props.data.style.ColumnName) === true
          ) {
            setCheckedState(true);
          } else {
            setCheckedState(false);
          }
        }
      } else if (
        openCanvasForPreview === "showDataAdditonSection" &&
        listviewClearState
      ) {
        if (props.data.id.includes("table")) {
          setCheckedState(false);
        }
      } else if ((window.isAboveSection || props.data.isAboveListview) && listviewClearState) {
        setCheckedState(false);
      }
    } else if (props.data.isAboveListview && listviewClearState) {
      const tableId = props.data?.id?.split("_")?.[0];
      let currentListviewColumnInfo = listviewColumnInfo.get(tableId);
      if (!currentListviewColumnInfo) {
        listviewColumnInfo.set(tableId, new Map());
        currentListviewColumnInfo = listviewColumnInfo.get(tableId);
      }
      currentListviewColumnInfo.set(props?.data?.style?.ColumnName, false);
      setCheckedState(false);
    } else if (openCanvasForPreview === "showDataAdditonSection") {
      const tableId = props.data?.id?.split("_")?.[0];
      let currentListviewColumnInfo = listviewColumnInfo.get(tableId);
      if (!currentListviewColumnInfo) {
        listviewColumnInfo.set(tableId, new Map());
        currentListviewColumnInfo = listviewColumnInfo.get(tableId);
      }
      currentListviewColumnInfo.set(props?.data?.style?.ColumnName, false);
      setCheckedState(false);
    }
  }, [listviewClearState, selectedRowIndex, listviewInfoUpdated]);

  function removeIdFromMandatoryControl(value) {
    if (mandatoryControls.includes(props.data.id)) {
      if (value === true || value === "true") {
        const newMandatoryList = mandatoryControls.filter(
          (item) => item !== props.data.id
        );
        setMandatoryControls([...newMandatoryList]);
      }
    }
  }

  function handleChange(event) {
    event.preventDefault();
    event.stopPropagation();
    if (window.ZoneGotFocus && props.data.style?.Zone && formState2?.Zonelist) {
      const selected = formState2.Zonelist.filter(
        (zone) => zone.name.toString() === props.data.style.Zone.toString()
      );
      callZoneGotFocusWithPreHook({ selected, controlId: props.data.id });
    }
    setCheckedState(!checkedState);
    if (
      (openCanvasForPreview === "showDataAdditonSection" ||
        props.data?.isAboveListview) &&
      props.data.style.hasOwnProperty("ColumnName")
    ) {
      dispatch(
        updateGridContexts({
          key: props.containerId,
          name: "listviewClearState",
          value: false,
        })
      );
      const tableId = props.data?.id?.split("_")?.[0];
      let currentListviewColumnInfo = listviewColumnInfo.get(tableId);
      if (!currentListviewColumnInfo) {
        listviewColumnInfo.set(tableId, new Map());
        currentListviewColumnInfo = listviewColumnInfo.get(tableId);
      }
      currentListviewColumnInfo.set(props.data.style.ColumnName, !checkedState);
    } else if (props.previewmode) {
      var jsonToSend = {
        attrValue: !checkedState,
        attrType: "boolean",
        attrName: props.data.dataclass || userDefinedId || props.data.id,
      };
      sendRequestToUpdateValue(jsonToSend, props?.containerId).then(
        (response) => {
          props.data.value = !checkedState;
          updateValue(props.data.id, !checkedState, props.containerId);
          if (
            props.data &&
            props.data.event &&
            props.data.event.Events &&
            props.data.event.Events.Click
          )
            triggerEventCalls(
              event,
              { id: props?.data?.id },
              props.data.event,
              props.data.elem_type,
              showAlertConfirmNotifications,
              reduxContexts
            );
        }
      );
    }
  }

  var mandatoryElem = null;
  if (
    props.data.style &&
    props.data.style["Mandatory"] !== undefined &&
    props.data.style["Mandatory"] !== null &&
    props.data.style["Mandatory"] === "true"
  ) {
    mandatoryElem = (
      <>
        <span
          style={{
            color: "#D53D3D",
            marginLeft: translationStyles1 ? "" : "0px",
            marginRight: translationStyles1 ? "0px" : "",
          }}
        >
          {" "}
          *{" "}
        </span>
      </>
    );
  }

  var checkboxLabelDefaultStyle = {
    color: "#000000",
    "font-family": "Open Sans",
    "font-size": "12px",
    "font-weight": "400",
    position: "relative",
  };
  const [rerenderState, setReRenderState] = useState(false);

  const setCheckBoxStyle = (newStyle) => {
    const prevState = JSON.stringify(checkBoxThemeStyle.current);
    checkBoxThemeStyle.current = { ...checkBoxThemeStyle.current, ...newStyle };
    if (!props.previewmode) {
      if (prevState !== JSON.stringify(checkBoxThemeStyle.current)) {
        setReRenderState(!rerenderState);
      }
    }
  };

  useEffect(() => {
    if (
      themeData?.["BasicTheme"]?.["FontFamily"] &&
      (!controlThemeData["LabelFontFamily"] ||
        controlThemeData["LabelFontFamily"] === "Select") &&
      (!propsDataStyle["FontFamily"] ||
        propsDataStyle["FontFamily"] === "Select")
    ) {
      setCheckBoxStyle({ fontFamily: themeData["BasicTheme"]["FontFamily"] });
    }
    if (
      (themeData?.["BasicTheme"]?.["FontFamily"] === "" ||
        themeData?.["BasicTheme"]?.["FontFamily"] === "Select") &&
      (!controlThemeData?.["LabelFontFamily"] ||
        controlThemeData?.["LabelFontFamily"] === "Select") &&
      (!propsDataStyle?.["FontFamily"] ||
        propsDataStyle?.["FontFamily"] === "Select")
    ) {
      setCheckBoxStyle({ fontFamily: defaultBuffer2.fontFamily });
    }
    if (
      themeData?.["BasicTheme"]?.["FontSize"] &&
      !controlThemeData["LabelFontSize"] &&
      (!propsDataStyle["FontSize"] || propsDataStyle["FontSize"] === "Select")
    ) {
      setCheckBoxStyle({ fontSize: themeData["BasicTheme"]["FontSize"] });
    }
    if (
      themeData?.["BasicTheme"]?.["ActiveTextColor"] &&
      !controlThemeData?.["LabelFontColor"] &&
      !propsDataStyle["FontColor"]
    ) {
      setCheckBoxStyle({
        color: themeData?.["BasicTheme"]?.["ActiveTextColor"],
      });
    }
  }, [themeData?.["BasicTheme"]]);

  useEffect(() => {
    if (
      themeData &&
      themeData?.["ToggleStates"]?.["ActiveStyle"] &&
      !propsDataStyle?.["ActiveStateColor"]
    ) {
      if (checkedState + "" === "true") {
        setCheckBoxStyle({
          stateColor: themeData["ToggleStates"]["ActiveStyle"],
        });
      }
    }
    if (themeData && themeData?.["ToggleStates"]?.["InActiveStyle"]) {
      setCheckBoxStyle({
        inActiveStateColor: themeData["ToggleStates"]["InActiveStyle"],
      });
    }
  }, [JSON.stringify(themeData?.["ToggleStates"]), checkedState]);

  useEffect(() => {
    if (
      controlThemeData &&
      controlThemeData["ControlStyleCheckbox"] &&
      !propsDataStyle["ControlStyle"]
    ) {
      setCheckBoxStyle({
        styleLayout: controlThemeData["ControlStyleCheckbox"],
      });
    }
    if (
      controlThemeData &&
      controlThemeData["LabelFontFamily"] &&
      (!propsDataStyle["FontFamily"] ||
        propsDataStyle["FontFamily"] === "Select")
    ) {
      setCheckBoxStyle({ fontFamily: controlThemeData["LabelFontFamily"] });
    }
    if (
      controlThemeData &&
      (controlThemeData["LabelFontFamily"] === "" ||
        controlThemeData["LabelFontFamily"] === "Select") &&
      (!propsDataStyle?.["FontFamily"] ||
        propsDataStyle?.["FontFamily"] === "Select")
    )
      setCheckBoxStyle({
        fontFamily:
          themeData?.["BasicTheme"]?.["FontFamily"] &&
          themeData?.["BasicTheme"]?.["FontFamily"] !== "Select"
            ? themeData?.["BasicTheme"]?.["FontFamily"]
            : defaultBuffer2.fontFamily,
      });
    if (
      controlThemeData &&
      controlThemeData["LabelFontSize"] &&
      (!propsDataStyle["FontSize"] || propsDataStyle["FontSize"] === "Select")
    ) {
      setCheckBoxStyle({ fontSize: controlThemeData["LabelFontSize"] });
    }
    if (
      controlThemeData &&
      (controlThemeData["LabelFontSize"] === "" ||
        controlThemeData["LabelFontSize"] === "Select") &&
      (!propsDataStyle?.["FontSize"] ||
        propsDataStyle?.["FontSize"] === "Select")
    ) {
      setCheckBoxStyle({
        fontSize:
          themeData?.["BasicTheme"]?.["FontSize"] &&
          themeData?.["BasicTheme"]?.["FontSize"] !== "Select"
            ? themeData?.["BasicTheme"]?.["FontSize"]
            : defaultBuffer2.fontSize,
      });
    }
    if (
      controlThemeData &&
      controlThemeData["LabelFontWeight"] &&
      (!propsDataStyle["FontWeight"] ||
        propsDataStyle["FontWeight"] === "Select")
    ) {
      if (controlThemeData["LabelFontWeight"].toLowerCase() === "italic") {
        setCheckBoxStyle({ fontStyle: "italic" });
        setCheckBoxStyle({ fontWeight: 300 });
      } else if (controlThemeData["LabelFontWeight"].toLowerCase() === "bold") {
        setCheckBoxStyle({ fontStyle: "normal" });
        setCheckBoxStyle({ fontWeight: "bold" });
      } else if (
        controlThemeData["LabelFontWeight"].toLowerCase() === "normal"
      ) {
        setCheckBoxStyle({ fontStyle: "normal" });
        setCheckBoxStyle({ fontWeight: 300 });
      } else if (
        controlThemeData["LabelFontWeight"].toLowerCase() === "bold italic"
      ) {
        setCheckBoxStyle({ fontStyle: "italic" });
        setCheckBoxStyle({ fontWeight: "bold" });
      }
    }
    if (
      controlThemeData &&
      (controlThemeData["LabelFontWeight"] === "" ||
        controlThemeData["LabelFontWeight"] === "Select") &&
      (!propsDataStyle?.["FontWeight"] ||
        propsDataStyle?.["FontWeight"] === "Select")
    ) {
      setCheckBoxStyle({ fontStyle: defaultBuffer2.fontStyle });
      setCheckBoxStyle({ fontWeight: defaultBuffer2.fontWeight });
    }
    if (
      controlThemeData &&
      controlThemeData["LabelFontColor"] &&
      !propsDataStyle["FontColor"]
    ) {
      setCheckBoxStyle({ color: controlThemeData["LabelFontColor"] });
    }
  }, [controlThemeData]);

  useEffect(() => {
    if (propsDataStyle["ControlStyle"]) {
      setCheckBoxStyle({ styleLayout: propsDataStyle["ControlStyle"] });
    }
    if (propsDataStyle["FontColor"]) {
      setCheckBoxStyle({ color: propsDataStyle["FontColor"] });
    }
    if (propsDataStyle["FontFamily"]) {
      setCheckBoxStyle({ fontFamily: propsDataStyle["FontFamily"] });
    }
    if (
      propsDataStyle?.["FontFamily"] === "" ||
      propsDataStyle?.["FontFamily"] === "Select"
    )
      setCheckBoxStyle({
        fontFamily:
          controlThemeData?.["LabelFontFamily"] &&
          controlThemeData?.["LabelFontFamily"] !== "Select"
            ? controlThemeData?.["LabelFontFamily"]
            : themeData?.["BasicTheme"]?.["FontFamily"] &&
              themeData?.["BasicTheme"]?.["FontFamily"] !== "Select"
            ? themeData?.["BasicTheme"]?.["FontFamily"]
            : defaultBuffer2.fontFamily,
      });
    if (propsDataStyle["FontWeight"]) {
      setCheckBoxStyle({ fontWeight: propsDataStyle["LabelWeight"] });
    }
    if (propsDataStyle["FontStyle"]) {
      setCheckBoxStyle({ fontStyle: propsDataStyle["FontStyle"] });
    }
    if (
      propsDataStyle?.["FontWeight"] === "" ||
      propsDataStyle?.["FontWeight"] === "Select"
    ) {
      if (
        controlThemeData?.["LabelFontWeight"] === "" ||
        controlThemeData?.["LabelFontWeight"] === "Select"
      ) {
        setCheckBoxStyle({ fontStyle: defaultBuffer2.fontStyle });
        setCheckBoxStyle({ fontWeight: defaultBuffer2.fontWeight });
      } else {
        if (controlThemeData["LabelFontWeight"].toLowerCase() === "italic") {
          setCheckBoxStyle({ fontStyle: "italic" });
          setCheckBoxStyle({ fontWeight: 300 });
        } else if (
          controlThemeData["LabelFontWeight"].toLowerCase() === "bold"
        ) {
          setCheckBoxStyle({ fontStyle: "normal" });
          setCheckBoxStyle({ fontWeight: "bold" });
        } else if (
          controlThemeData["LabelFontWeight"].toLowerCase() === "normal"
        ) {
          setCheckBoxStyle({ fontStyle: "normal" });
          setCheckBoxStyle({ fontWeight: 300 });
        } else if (
          controlThemeData["LabelFontWeight"].toLowerCase() === "bold italic"
        ) {
          setCheckBoxStyle({ fontStyle: "italic" });
          setCheckBoxStyle({ fontWeight: "bold" });
        }
      }
    }
    if (propsDataStyle["FontSize"]) {
      setCheckBoxStyle({ fontSize: propsDataStyle["FontSize"] });
    }
    if (
      propsDataStyle?.["FontSize"] === "" ||
      propsDataStyle?.["FontSize"] === "Select"
    ) {
      setCheckBoxStyle({
        fontSize:
          controlThemeData?.["LabelFontSize"] &&
          controlThemeData?.["LabelFontSize"] !== "Select"
            ? controlThemeData?.["LabelFontSize"]
            : themeData?.["BasicTheme"]?.["FontSize"] &&
              themeData?.["BasicTheme"]?.["FontSize"] !== "Select"
            ? themeData?.["BasicTheme"]?.["FontSize"]
            : defaultBuffer2.fontSize,
      });
    }
    if (propsDataStyle["ActiveStateColor"]) {
      if (checkedState + "" === "true") {
        setCheckBoxStyle({ stateColor: propsDataStyle["ActiveStateColor"] });
      }
    }
    if (propsDataStyle["SpacingLeft"]) {
      if (translationStyles1)
        setCheckBoxStyle({ paddingRight: propsDataStyle["SpacingLeft"] });
      else setCheckBoxStyle({ paddingLeft: propsDataStyle["SpacingLeft"] });
    }
    if (propsDataStyle["SpacingRight"]) {
      if (translationStyles1)
        setCheckBoxStyle({ paddingLeft: propsDataStyle["SpacingRight"] });
      else setCheckBoxStyle({ paddingRight: propsDataStyle["SpacingRight"] });
    }
    if (propsDataStyle["SpacingTop"])
      setCheckBoxStyle({ paddingTop: propsDataStyle["SpacingTop"] });
    if (propsDataStyle["SpacingBottom"])
      setCheckBoxStyle({ paddingBottom: propsDataStyle["SpacingBottom"] });
  }, [JSON.stringify(propsDataStyle), checkedState]);

  var themeStyle = {
    checkboxLabelDefaultStyle: checkboxLabelDefaultStyle,
  };

  const inputStyle = {
    lineHeight: "1",
    marginLeft:
      checkBoxThemeStyle.current.styleLayout === "Style1"
        ? translationStyles1
          ? null
          : "0px"
        : "0px",
    marginRight:
      checkBoxThemeStyle.current.styleLayout === "Style1" && translationStyles1
        ? "0px"
        : null,
    marginRight:
      window?.depsatkur_configs?.selectedLanguage === "ar" ||
      window?.depsatkur_configs?.selectedLanguage === "ar-SA"
        ? "15px"
        : "0px",
    position: "relative",
    "word-break": "break-word",
    "white-space": "break-spaces",
    marginBottom: "0px",
    alignItems: "flex-start",
    color: checkBoxThemeStyle.current.color,
    fontSize: checkBoxThemeStyle.current.fontSize,
    fontFamily: checkBoxThemeStyle.current.fontFamily,
    fontStyle: checkBoxThemeStyle.current.fontStyle,
    fontWeight: checkBoxThemeStyle.current.fontWeight,
  };

  const inputStyle2 = {
    lineHeight: "1.45",
    display: "inline",
    position: "relative",
    "margin-top": "1px",
    "word-break": "break-word",
  };
  const inputStyleForCheckbox = {
    color: checkBoxThemeStyle.current.stateColor,
    width: checkBoxThemeStyle.current.fontSize,
    alignItems:
      checkBoxThemeStyle.current.styleLayout === "Style1" ? "flex-start" : "",
    alignSelf:
      checkBoxThemeStyle.current.styleLayout === "Style1" ? "flex-start" : "",
    height: checkBoxThemeStyle.current.fontSize,
  };
  useEffect(() => {
    removeIdFromMandatoryControl(checkedState);
  }, [checkedState]);

  const hexToRgb = (hex) =>
    hex
      .replace(
        /^#?([a-f\d])([a-f\d])([a-f\d])$/i,
        (m, r, g, b) => "#" + r + r + g + g + b + b
      )
      .substring(1)
      .match(/.{2}/g)
      .map((x) => parseInt(x, 16));

  const [codehexToRgb, setCodehexToRgb] = useState();

  useEffect(() => {
    if (
      checkBoxThemeStyle.current.stateColor &&
      checkBoxThemeStyle.current.stateColor !== null
    ) {
      setCodehexToRgb(hexToRgb(checkBoxThemeStyle.current.stateColor));
    }
  }, [checkedState, checkBoxThemeStyle.current.stateColor]);

  var fin = "rgba(";
  if (codehexToRgb && codehexToRgb !== null) {
    for (var i = 0; i < codehexToRgb.length; i++) {
      fin += codehexToRgb[i];
      if (i != codehexToRgb.length - 1) fin += ", ";
    }
    fin += ")";
  }

  const useStyles2 = makeStyles({
    root: {},
    icon: {
      border: (style) => (style.showMandatoryMsg ? "1px solid red" : ""),
      borderRadius: 3,
      width: checkBoxThemeStyle.current.fontSize,
      height: checkBoxThemeStyle.current.fontSize,
      boxShadow:
        "inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1)",
      position: "absolute",
      left:
        window?.depsatkur_configs?.selectedLanguage === "ar" ||
        window?.depsatkur_configs?.selectedLanguage === "ar-SA"
          ? null
          : "0px",
      right:
        window?.depsatkur_configs?.selectedLanguage === "ar" ||
        window?.depsatkur_configs?.selectedLanguage === "ar-SA"
          ? "0px"
          : null,
      backgroundImage:
        "linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0))",
      "$root.Mui-focusVisible &": {
        outline: "2px auto rgba(19,124,189,.6)",
        outlineOffset: 2,
      },
      "input:hover ~ &": {},
      "input:disabled ~ &": {
        boxShadow: "none",
        background: checkBoxThemeStyle.current.inActiveStateColor,
      },
      backgroundColor: "#fff",
    },
    checkedIcon: {
      backgroundImage:
        "linear-gradient(180deg,hsla(0,0%,100%,.1),hsla(0,0%,100%,0))",
      "&:before": {
        display: "block",
        width: checkBoxThemeStyle.current.fontSize,
        height: checkBoxThemeStyle.current.fontSize,
        backgroundImage:
          "url(\"data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3E%3Cpath" +
          " fill-rule='evenodd' clip-rule='evenodd' d='M12 5c-.28 0-.53.11-.71.29L7 9.59l-2.29-2.3a1.003 " +
          "1.003 0 00-1.42 1.42l3 3c.18.18.43.29.71.29s.53-.11.71-.29l5-5A1.003 1.003 0 0012 5z' fill='" +
          fin +
          "'/%3E%3C/svg%3E\")",
        content: '""',
      },
      "input:hover ~ &": {},
      backgroundColor: "#fff",
    },
    ".MuiSvgIcon-root": {
      color:
        (props.data.style["Enable"] === "true" ||
          props.data.style["Enable"] === true) &&
        (props.data.style["ReadOnly"] === "false" ||
          props.data.style["ReadOnly"] === false) &&
        (props.data.style["ReadOnlyStyle"] === "false" ||
          props.data.style["ReadOnlyStyle"] === false)
          ? ""
          : checkBoxThemeStyle?.current?.inActiveStateColor,
      backgroundColor:
        (props.data.style["Enable"] === "true" ||
          props.data.style["Enable"] === true) &&
        (props.data.style["ReadOnly"] === "false" ||
          props.data.style["ReadOnly"] === false) &&
        (props.data.style["ReadOnlyStyle"] === "false" ||
          props.data.style["ReadOnlyStyle"] === false)
          ? "#fff"
          : checkBoxThemeStyle?.current?.inActiveStateColor,
      width: checkBoxThemeStyle.current.fontSize,
      fontSize: checkBoxThemeStyle.current.fontSize,
      height: checkBoxThemeStyle.current.fontSize,
    },
  });

  const StyledCheckbox = (props) => {
    const classes = useStyles2(props.style);
    return (
      <Checkbox
        className={classes.root}
        disableRipple
        checkedIcon={
          <span className={clsx(classes.icon, classes.checkedIcon)} />
        }
        icon={<span className={classes.icon} />}
        inputProps={{ "aria-label": "decorative checkbox" }}
        {...props}
      />
    );
  };
  const inputStylePadding = {
    paddingLeft: checkBoxThemeStyle.current.paddingLeft,
    paddingRight: checkBoxThemeStyle.current.paddingRight,
    paddingTop: checkBoxThemeStyle.current.paddingTop,
    paddingBottom: checkBoxThemeStyle.current.paddingBottom,
    display: checkBoxThemeStyle.current.display,
    alignItems: "center",
  };

  const handleBlur = (e) => {
    if (window.ZoneLostFocus) {
      window.ZoneLostFocus();
    }
  };

  if (props.previewmode) {
    return (
      <>
        {props.data &&
        props.data.style &&
        props.data.style["Visible"] === "true" ? (
          <Form.Group
            className={[
              classes.rootMain,
              styles2.formStyle + " spacingThemeControls ",
            ].join(" ")}
            style={
              mobileDisplay === false || tabDisplay === false
                ? { display: "none" }
                : inputStylePadding
            }
            control_id={userDefinedControlId + "_group"}
            controlId="dob"
            draggable={true}
            onDragStart={props.handleReorderStart}
            onDrop={props.handleReorderDrop}
            data-placement="top"
            key={props.id}
            elem_type="checkbox"
            frameindex={props.data.frameIndex}
            elementindex={props.data.elementIndex}
            sheetIndex={props.data.sheetIndex}
            sheetElementIndex={props.data.sheetElementIndex}
            sheetFrameElementindex={props.data.sheetFrameElementindex}
          >
            <PrepareCSSClass stylingData={themeStyle} />
            {checkBoxThemeStyle.current.styleLayout === "Style1" ? (
              <StyledCheckbox
                type="checkbox"
                id={userDefinedControlId}
                control_id={userDefinedControlId}
                elem_type="checkbox"
                frameindex={props.data.frameIndex}
                elementindex={props.data.elementIndex}
                checked={checkedState}
                value={checkedState}
                inputRef={elementRefrence}
                onClick={(e) => handleChange(e)}
                onBlur={(e) => handleBlur(e)}
                style={{ ...inputStyleForCheckbox, showMandatoryMsg }}
                size="small"
                disabled={
                  props?.data?.style?.["Enable"] === "false" ||
                  props?.data?.style?.["Enable"] === false ||
                  props?.data?.style?.["ReadOnly"] === "true" ||
                  props?.data?.style?.["ReadOnly"] === true ||
                  props?.data?.style?.["ReadOnlyStyle"] === "true" ||
                  props?.data?.style?.["ReadOnlyStyle"] === true
                    ? true
                    : false
                }
              />
            ) : (
              <Checkbox
                type="checkbox"
                id={userDefinedControlId}
                className={classes.rootMain}
                control_id={userDefinedControlId}
                elem_type="checkbox"
                frameindex={props.data.frameIndex}
                elementindex={props.data.elementIndex}
                checked={checkedState}
                value={checkedState}
                inputRef={elementRefrence}
                onClick={(e) => handleChange(e)}
                onBlur={(e) => handleBlur(e)}
                style={
                  showMandatoryMsg
                    ? { ...inputStyleForCheckbox, color: "red" }
                    : { ...inputStyleForCheckbox }
                }
                disabled={
                  props?.data?.style?.["Enable"] === "false" ||
                  props?.data?.style?.["Enable"] === false ||
                  props?.data?.style?.["ReadOnly"] === "true" ||
                  props?.data?.style?.["ReadOnly"] === true ||
                  props?.data?.style?.["ReadOnlyStyle"] === "true" ||
                  props?.data?.style?.["ReadOnlyStyle"] === true
                    ? true
                    : false
                }
              />
            )}

            <Form.Label
              style={inputStyle}
              className={"checkboxLabelDefaultStyle"}
              elem_type="checkbox"
              id={userDefinedControlId + "_label"}
              control_id={userDefinedControlId + "_label"}
              frameindex={props.data.frameIndex}
              elementindex={props.data.elementIndex}
            >
              <span
                style={{
                  marginLeft: translationStyles1 ? "" : "8px",
                  marginRight: translationStyles1 ? "8px" : "",
                  display: "flex",
                  alignItems: "flex-end",
                  fontSize: checkBoxThemeStyle.current.fontSize,
                }}
              >
                {props?.data?.style?.["Label"] ||
                props?.data?.style?.["Label"] === ""
                  ? props?.data?.style?.["Label"]
                  : props?.data?.id}
              </span>
              {mandatoryElem}
              {checkValid(props.data.style.ToolTip) && (
                <span style={{ display: "inline-block" }}>
                  <Tooltip tooltip={props.data.style.ToolTip} />{" "}
                </span>
              )}
            </Form.Label>
          </Form.Group>
        ) : null}

        {showMandatoryMsg ? (
          <MandatoryMsg
            id={props.data.id}
            mandatoryMsg={props.data?.style?.ValidationMessage}
          />
        ) : null}
      </>
    );
  } else {
    return (
      <Form.Group
        onClick={
          isSubformInPage
            ? () => {}
            : (e) => props.handleSelected(e, props.id, "n")
        }
        onKeyDown={(e) => props.handleSelected(e)}
        className={[
          classes.rootMain,
          styles3.formStyle + " spacingThemeControls ",
        ].join(" ")}
        style={inputStylePadding}
        control_id={props.id}
        controlId="dob"
        draggable={true}
        tabIndex="1"
        onDragStart={props.handleReorderStart}
        onDrop={props.handleReorderDrop}
        data-placement="top"
        key={props.id}
        elem_type="checkbox"
        frameindex={props.data.frameIndex}
        elementindex={props.data.elementIndex}
        sheetIndex={props.data.sheetIndex}
        sheetElementIndex={props.data.sheetElementIndex}
        sheetFrameElementindex={props.data.sheetFrameElementindex}
      >
        <EditableLabel
          defaultLabel={
            <span control_id={props.id}>
              <Form.Label
                onDoubleClick={bringEditableLabel}
                style={inputStyle}
                className={"checkboxLabelDefaultStyle"}
                elem_type="checkbox"
                id={userDefinedControlId + "Label"}
                control_id={props.id}
                frameindex={props.data.frameIndex}
                elementindex={props.data.elementIndex}
              >
                {checkBoxThemeStyle.current.styleLayout === "Style1" ? (
                  <StyledCheckbox
                    type="checkbox"
                    id={userDefinedControlId}
                    control_id={props.id}
                    elem_type="checkbox"
                    frameindex={props.data.frameIndex}
                    elementindex={props.data.elementIndex}
                    checked={checkedState}
                    value={checkedState}
                    onClick={(e) => handleChange(e)}
                    style={{ ...inputStyleForCheckbox, showMandatoryMsg }}
                    size="small"
                  />
                ) : (
                  <Checkbox
                    className={classes.rootMain}
                    type="checkbox"
                    id={userDefinedControlId}
                    control_id={props.id}
                    elem_type="checkbox"
                    frameindex={props.data.frameIndex}
                    elementindex={props.data.elementIndex}
                    checked={checkedState}
                    style={{ ...inputStyleForCheckbox }}
                    value={checkedState}
                    inputRef={elementRefrence}
                    onClick={(e) => handleChange(e)}
                  />
                )}
                <span
                  style={{
                    marginLeft: translationStyles1 ? "" : "8px",
                    marginRight: translationStyles1 ? "8px" : "",
                    lineHeight: "1",
                    display: "flex",
                    alignItems: "flex-end",
                    fontSize: checkBoxThemeStyle.current.fontSize,
                  }}
                >
                  {props?.data?.style?.["Label"] ||
                  props?.data?.style?.["Label"] === ""
                    ? props?.data?.style?.["Label"]
                    : props?.data?.id}
                </span>
                {mandatoryElem}
                {checkValid(props.data.style.ToolTip) && (
                  <span
                    style={{ display: "inline-block" }}
                    control_id={props.id}
                  >
                    <Tooltip
                      control_id={props.id}
                      tooltip={props.data.style.ToolTip}
                    />
                  </span>
                )}
              </Form.Label>
            </span>
          }
          editlabel={editLabel}
          default_value={defaultLabel}
          setLabelHandler={(recievedLabel) => setLabelHandler(recievedLabel)}
          style={inputStyle2}
          maxLength={true}
          control_id={props.data.id}
        />

        <>&nbsp;</>
        {controlSelected?.id === props?.id && (
          <ElemDeleteControl
            isSubformInPage={isSubformInPage}
            containerId={props.containerId}
            id={props.data.id}
            deleteControl={deleteControl}
            elemType={props.data.elem_type}
            setDragging={props.setDragging}
            handleReorderStart={props.handleReorderStart}
            handleReorderDrop={props.handleReorderDrop}
            data={props.data}
            dragId={props.dragId}
            dragType={"column"}
          />
        )}
        {window?.openQuickTools && open && (
          <QuickTools
            containerId={props.containerId}
            selectedControl={props.data}
            open={open}
            setOpen={setOpen}
          />
        )}
        {alertData.open === true && <CustomizedAlert alertData={alertData} />}
      </Form.Group>
    );
  }
};

export default CHECKBOX;
